html {
  width: 100%;
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  background-color: #111111;
  color: white;
}

p {
  font-size: 2.2rem;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 3.4rem;
  font-weight: 700;
}

img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 2.5rem 0;
  text-align: center;
}

h1 {
  font-size: 7.6rem;
}

h2 {
  font-size: 5rem;
  font-family: "Lora", serif;
}

h3 {
  font-size: 3.4rem;
}

h4 {
  font-size: 2.2rem;
}

.hero {
  height: 100vh;
  min-height: 60rem;
  /* =600px */
  background-image: url(/build/img/WEB-02.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.barra {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#111111+0,111111+100&1+0,0+100 */
  background: -moz-linear-gradient(top, #111111 0%, rgba(17, 17, 17, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #111111 0%, rgba(17, 17, 17, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #111111 0%, rgba(17, 17, 17, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#111111", endColorstr="#00111111",GradientType=0 );
  /* IE6-9 */
}
.barra .contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.barra img {
  height: 15rem;
}
.barra .navegacion {
  display: flex;
  column-gap: 3rem;
}
.barra .navegacion-enlace {
  color: white;
}

.slogan {
  display: flex;
  justify-content: flex-end;
}
.slogan img {
  max-height: 30rem;
}

.barra-inversa {
  height: 15rem;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#7db9e8+0,111111+100&0+0,1+100 */
  background: -moz-linear-gradient(top, rgba(125, 185, 232, 0) 0%, #111111 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(125, 185, 232, 0) 0%, #111111 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(125, 185, 232, 0) 0%, #111111 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#007db9e8", endColorstr="#111111",GradientType=0 );
  /* IE6-9 */
}

.galeria {
  width: 95%;
  margin: 0 auto;
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-auto-flow: column;
  gap: 0.5rem;
}
.galeria img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: grayscale(80%);
}
.galeria img:hover {
  filter: grayscale(0%);
  cursor: pointer;
}
.galeria .imagen-1 {
  grid-column: 1/6;
}
.galeria .imagen-2 {
  grid-column: 6/9;
}
.galeria .imagen-3 {
  grid-column: 9/13;
  grid-row: 1/3;
}
.galeria .imagen-5 {
  grid-column: 1/4;
}
.galeria .imagen-4 {
  grid-column: 4/9;
}
.galeria .imagen-6 {
  grid-column: 1/7;
}
.galeria .imagen-7 {
  grid-column: 7/10;
}
.galeria .imagen-8 {
  grid-column: 10/13;
}
.galeria .imagen-11 {
  grid-column: 1/5;
}
.galeria .imagen-10 {
  grid-column: 5/8;
}
.galeria .imagen-9 {
  grid-column: 8/13;
}

.integrantes {
  display: flex;
  justify-content: space-evenly;
}
.integrantes .integrante {
  text-align: center;
}
.integrantes img {
  height: 25rem;
  margin: 0 auto;
}

.info {
  display: flex;
  justify-content: space-between;
}
.info .datos {
  margin: auto 0;
}
.info p {
  margin-bottom: 0;
}
.info h3 {
  margin-top: 0;
  text-align: left;
}

.redes {
  display: flex;
  justify-content: center;
  column-gap: 3rem;
  margin-bottom: 10rem;
}